


























































.about-company {
  width: 100%;
  padding: 0 12.6%;

  #about_scene {
    position: relative;
    width: 100%;
    height: 60vh;
  }
}

.dots-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.denis_popov-img {
  position: absolute;
  right: 0;
  top: 0;
}

.denis_popov-ceo {
  position: absolute;
  right: 0;
  bottom: 11%;
  width: 617px;
  height: 123px;

  .ceo {
    position: absolute;
    top: 0;
    left: 0;
    background: #1E30F4;
    width: 617px;
    height: 123px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column nowrap;
    clip-path: url(#boxClipPath);

    .header_text, .blue_text {
      color: #FFFFFF;
      text-align: left;
      width: 375px;
      white-space: nowrap;
    }

    .header_text {
      font-size: 24px;
      line-height: 24px;
      letter-spacing: 0.2em;
      text-transform: uppercase;
    }

    .blue_text {
      margin: 10px 0;
      font-size: 18px;
      line-height: 18px;
    }
  }
}

.hiddenClipPath {
  height: 0;
  width: 0;
  position: absolute;
}
.svgPolygon{
  position: absolute;
  &.svgCircle{
    bottom: 20%;
    left: 10%;
  }
  &.svgBlueCircle{
    top: 40%;
    right: 10%;
  }
  &.svgTriangle{
    top: 10%;
    left: 60%;
  }
}

.dots {
  position: absolute;
  width: 687.38px;
  height: 194.71px;
  background: url("~@/assets/img/dot.svg") space;

  &-horizontal {

    right: 8.68%;
    bottom: 106px;
  }

  &-vertical {
    transform: rotate(90deg);
    left: -18%;
    top: 16%;
  }
}
.about-company-text{
  position: absolute;
  width: 491px;
  top: 20%;
  left: 20%;
  &:before{
    position: absolute;
    content: '';
    display: block;
    width: 7px;
    height: 100%;
    background: #0FF8F1;
    top: 0;
    left: -25px;
  }
  .text{
    font-size: 18px;
    line-height: 27px;
    color: #150F0F;
  }
}
.main_back {
  width: 100%;
  min-width: 1221px;
  height: 791px;
  min-height: 604px;

  background: url("~@/assets/img/about_main_background.png") no-repeat;
  background-size: 100%;
}
