

































.insta_container{
  padding: 0 12.6%;
}
.insta_wrapper{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  gap: 0;
  .img_wrapper{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    cursor: pointer;
    .img_descr{
      display: flex;
      justify-content: flex-end;
      flex-flow: column nowrap;
      position: absolute;
      opacity: 0;
      z-index: 10;
      height: 100%;
      width: 100%;
      padding: 20px;
      background: rgba(30, 48, 244, 0.8);
      transition: opacity  0.3s 100ms ease;
      .stats{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 17px;
        .like_img{
          margin-right: 10px;
        }
        .comment_img{
          margin-left: 24px;
        }
        .count{
          font-size: 18px;
          line-height: 150%;
          letter-spacing: 1.06039px;
          color: #FFFFFF;
        }
      }
      p{
        width: 100%;
        color: #FFFFFF;
        font-size: 18px;
        line-height: 130%;
        margin: 0;
      }
    }

    .img{
      width: 120%;
      //height: 100%;
    }
  }
  .img_wrapper:hover{
    .img_descr{
      opacity: 1;
    }
  }
}

