









































































.facts{
  position: relative;
  margin-bottom: 150px;
  .glide__arrows{
    .glide__arrow{
      display: block;
      position: absolute;
      width: 50px;
      height: 50px;
      z-index: 1;
      background: url("~@/assets/icons/arrow.svg") center no-repeat;
      background-size: contain;
      border: 0;
      cursor: pointer;
      &--left{
        left: 5%;
        top: 50%;
      }
      &--right{
        right: 5%;
        top: 50%;
        transform: rotate(180deg);
      }
    }
  }
  .glide__slide{
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
    padding: 0 12.6vw;
  }
}
.factoidMORE{
  grid-auto-columns: 1fr 2fr;
}
video{
  cursor: pointer;
}
.video_container{
  position: relative;
  padding: 0 12.6%;
  .video_play{
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 25.2%);
    height: 100%;
    margin: 0 12.6%;
    cursor: pointer;
    &:after{
      position: absolute;
      content: '';
      top: calc(50% - 56px);
      left: calc(50% - 56px);
      width: 112px;
      height: 112px;
      background: url("~@/assets/img/play.svg") no-repeat center;
      background-size: 100%;
      cursor: pointer;
    }
  }
}
