









































.customers-wrapper{
  padding: 0 12.6%;
}
.customers{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-flow: row;
  grid-template-rows: repeat(3, 1fr);
  .icon_container:nth-child(13){
    grid-column: span 2;
    justify-self: start;
  }
  .icon_container:nth-child(11){
    grid-column: span 2;
    justify-self: end;

  }

  .icon_container{
    position: relative;
    display: flex;
    margin: 1.55vw;
    width: 196px;
    height: 117px;
    justify-content: center;
    align-items: center;
    .icon{
      position: absolute;
    }
    .icon-hover{
      position: absolute;
      z-index: 2;
      opacity: 0;
      transition: opacity 0.2s ease;
    }
    &:hover{
      .icon-hover{
        opacity: 1;
      }
    }
  }
}
