

























































































.reviews{
  width: 100%;
  .glide__slide{
    width: 100%;
    padding: 0 12.6vw;
    display: flex;
    .logo_container-review{
        position: relative;
        display: flex;
        flex: 37.14% 1 1;
        //padding: 37.14%;
        justify-content: center;
        align-items: center;
        background: #1C2CDD;
        transition: background-color 0.2s ease;
        .logo{

        }
        .logo-hover{
          position: absolute;
          z-index: 2;
          opacity: 0;
          transition: opacity 0.2s ease;
        }
        &:hover{
          background: #ffffff;
          .logo-hover{
            opacity: 1;
          }
        }
    }
    .review{
      flex: 70% 2 1;
      display: flex;
      flex-flow: column nowrap;
      padding: 40px 55px;
      box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.05);
      p{
        margin: 0;
      }
      .reviewer_name{
        font-size: 24px;
        line-height: 31px;
        letter-spacing: 0.2em;
        text-transform: uppercase;
      }
      .reviewer_position{
        font-size: 18px;
        line-height: 27px;
        margin: 10px 0 24px;
      }
      .reviewer_review {
        font-size: 18px;
        line-height: 27px;
        color: #150F0F;
      }
    }
  }
}
