





































































.communication{
  margin-top: 96px;
  box-sizing: border-box;
  width: 100%;
  height: 36vw;
  position: relative;
  perspective: 200vw;
}
.carousel {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  height: 36vw;
  width: inherit;
  position: absolute;
  transform-style: preserve-3d;
  transition: transform 1s;
}

.slide__item {
  display: block;
  position: absolute;
  background: #000;
  width: 49vw;
  height: 36vw;

  transform: scale(0.8);
  img{
    width: 100%;
    user-select: none;

  }
}

.a {
  transform: rotateY(0deg) translateZ(36vw) scale(0.8);
  background: #ed1c24;
}
.b {
  transform: rotateY(60deg) translateZ(36vw) scale(0.8);
  background: #0072bc;
}
.c {
  transform: rotateY(120deg) translateZ(36vw) scale(0.8);
  background: #39b54a;
}
.d {
  transform: rotateY(180deg) translateZ(36vw) scale(0.8);
  background: #f26522;
}
.e {
  transform: rotateY(240deg) translateZ(36vw) scale(0.8);
  background: #630460;
}
.f {
  transform: rotateY(300deg) translateZ(36vw) scale(0.8);
  background: #8c6239;
}

