















































.counter{
  position: relative;
  display: inline-flex;
  flex-flow: column nowrap;
  align-items: center;
  &:nth-child(n+2):before{
    display: block;
    content: '';
    border: 1px solid #000000;
    width: 0;
    height: 70px;
    position: absolute;
    left: 0;
    top: calc(20%);
  }
  .factoid_text {
    font-size: 60px;
    line-height: 90px;
    letter-spacing: 0.1em;
    text-align: center;

  }
  .descr{
    width: 100%;
    text-align: center;
  }
}


