
























.header{
  width: 100%;
  height: 100vh;
  position: relative;
}

.company_name{
  height: 200px;
  position: absolute;
  top: 70%;
  right: 11.5%;
  z-index: 10;
  &__name{
    font-size: 80px;
    line-height: 80px;
    margin: 0;
  }
  &__description{
    font-size: 28px;
    line-height: 42px;
    margin: 0;
  }
}
.scroll_down{
  position: absolute;
  left: calc(50% - 14.5px);
  bottom: 10%;
  width: 29px;
  height: 70px;
  background: url("~@/assets/icons/scroll.svg");
}
