






































































































































.glide__slides{
  overflow: visible !important;
}
.crew{
  width: 100%;
  .glide__slide{
    width: min-content;
    display: flex;
    .member_container{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;

      .details{
        position: absolute;
        bottom: 0;
        .member-name{
          color: #FFFFFF;
          font-size: 24px;
          line-height: 31px;
          text-align: center;
          letter-spacing: 0.2em;
          text-transform: uppercase;
          margin: 0;
        }
        .member-position{
          font-size: 18px;
          line-height: 27px;
          text-align: center;
          letter-spacing: 1px;
          color: #FFFFFF;
          margin-top: 10px;
        }
      }
    }
  }

}
.glide__slide:nth-child(odd) {

    padding-bottom: 8.75vw;

}
.glide__slide:nth-child(even) {

    padding-top: 8.75vw;

}
